import React, { Component } from "react"
import { Route, NavLink, HashRouter } from "react-router-dom";

import '../css/Main.css';

import About from "./About";
import Work from "./Work";
import Contact from "./Contact";

const contentList = [
    { 
        "Component": About, 
        "Link": "/",
        "LinkExact": true,
        "Header": "About" 
    },
    { 
        "Component": Work, 
        "Link": "/work",
        "LinkExact": false,
        "Header": "Work" 
    },
    { 
        "Component": Contact, 
        "Link": "/contact",
        "LinkExact": false,
        "Header": "Contact" 
    }
];

class Main extends Component {
    render() {
        const tabListItems = contentList.map(
            (obj) => <li key={obj.Header}><NavLink to={obj.Link}>{obj.Header}</NavLink></li>
        );

        const tabRouteItems = contentList.map(
            (obj) => <Route exact={obj.LinkExact} key={obj.Header} path={obj.Link} component={obj.Component} />
        );

        return (
            <HashRouter>
                <div>
                    <h1 className="header">
                        Ta David Yu
                    </h1>
                    <ul className="header">
                        {tabListItems}
                    </ul>
                    <div className="content">
                        {tabRouteItems}
                    </div>
                </div>
            </HashRouter>
        );
    }
}

export default Main;